html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

a {
  color: #e80049;
}

.m1-header {
  border-bottom: 2px solid #ccc;
  align-items: center;
  padding: 10px 10px 15px;
  display: flex;
}

.m1-header__cfml, .m1-header__hotwire, .m1-header__yay {
  color: #f0f0f0;
  background-color: #222;
  border-radius: 5px;
  padding: 5px 12px;
}

.m1-header__operator {
  margin: 0 10px;
}

.m1-header--active .m1-header__cfml {
  background-color: #01798a;
}

.m1-header--active .m1-header__hotwire {
  color: #000;
  background-color: #ffe801;
}

.m1-header--active .m1-header__yay {
  background-color: red;
}

